@import '../vars';

$sliderBg: #c4c4c4;
$sliderMainColor: $secondary-color;
$sliderTextColor: #4d4d4d;
$sliderThumbColor: #d9d9d9;

$sliderWidth: 170px;

.slider__container {
  box-sizing: border-box;
  width: 100%;
  padding: 20px 10px 50px;
}

.slider {
  position: relative;
  width: $sliderWidth;
}

.slider__track,
.slider__range,
.slider__left-value,
.slider__right-value {
  position: absolute;
}

.slider__track,
.slider__range {
  border-radius: 3px;
  height: 5px;
}

.slider__track {
  background-color: $sliderBg;
  width: 100%;
  z-index: 1;
}

.slider__range {
  background-color: $sliderMainColor;
  z-index: 2;
}

.slider__left-value,
.slider__right-value {
  color: $sliderTextColor;
  font-size: 12px;
  margin-top: 20px;
}

.slider__left-value {
  left: 6px;
}

.slider__right-value {
  right: -4px;
}

/* Removing the default appearance */
.thumb,
.thumb::-webkit-slider-thumb {
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
}

.thumb {
  pointer-events: none;
  position: absolute;
  height: 0;
  width: $sliderWidth;
  outline: none;
}

.thumb--left {
  z-index: 3;
}

.thumb--right {
  z-index: 4;
}

/* For Chrome browsers */
.thumb::-webkit-slider-thumb {
  background-color: $sliderThumbColor;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px $sliderBg;
  cursor: pointer;
  height: 18px;
  width: 18px;
  margin-top: 4px;
  pointer-events: all;
  position: relative;
}

/* For Firefox browsers */
.thumb::-moz-range-thumb {
  background-color: $sliderThumbColor;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 1px 1px $sliderBg;
  cursor: pointer;
  height: 18px;
  width: 18px;
  margin-top: 4px;
  pointer-events: all;
  position: relative;
}
